//generation deck

import PlayCard from "../../Class/PlayCard";

const Deck = () => {
    const ranks = ['2', '3', '4', '5', '6', '7', '8', '9', '10', 'Jack', 'Queen', 'King', 'Ace']; //cp bc ca l
    const suits = ['Heart', 'Club', 'Diamond', 'Spread'];//cht ca l bi
    const deck = suits.map((suit, indexSuit) => {
        return ranks.map((rank, indexRank) => {
            return new PlayCard(rank, suit, {
                width: indexRank * -142,
                height: indexSuit * -190
            })
        })
    }).flat() // flat gip bin 1 mng [[1,2,3,4],[5,6]] thnh [1,2,3,4,5,6]

    return deck
}

export default Deck